import React from 'react';
import {Timer} from "../Timer";
import {useDispatch, useSelector} from "react-redux";
import {RootState} from "../../store/reducers";
import {
    selectCanStart as selectCanStartTimer,
    selectCanDone as selectCanDoneTimer,
    selectCanPause as selectCanPauseTimer,
    selectCanStop as selectCanStopTimer,
    selectCanProlongTime as selectCanProlongTimeTimer,
    selectCanResume as selectCanResumeTimer,
    selectCanSkip as selectCanSkipTimer,
} from "../../store/selectors/timer";

import {
    manualTimerDone,
    timerPause,
    timerProlong,
    timerSkip,
    timerStartAsync,
    timerStop
} from "../../store/action-creators/timer";
import {TimerStatus, TimerType} from "../../store/types";

function selectTitleTimer(state:RootState):string{
    const {type, pomidorMumber, shortBreakCount} = state.timer;

    switch (type){
        case TimerType.LongBreak:
            return 'Сделайте длинный перерыв';
        case TimerType.ShortBreak:
            return `Сделайте короткий перерыв №${shortBreakCount}`;
        case TimerType.Pomidor:
            return `Помидор №${pomidorMumber}`;
    }
}

const selectFirstTaskName = (state:RootState) => state.taskList.tasks.length === 0?'Нет задачи':state.taskList.tasks[0].title;

const selectTotalSecondsTimer = (state:RootState) => state.timer.totalSeconds;
const selectStatusTimer = (state:RootState) => state.timer.status;
const selectCanStart = (state:RootState) => selectCanStartTimer(state.timer);
const selectCanStop = (state:RootState) => selectCanStopTimer(state.timer);
const selectCanPause = (state:RootState) => selectCanPauseTimer(state.timer);
const selectCanProlongTime = (state:RootState) => selectCanProlongTimeTimer(state.timer);
const selectCanResume = (state:RootState) => selectCanResumeTimer(state.timer);
const selectCanDone = (state:RootState) => selectCanDoneTimer(state.timer);
const selectCanSkip = (state:RootState) => selectCanSkipTimer(state.timer);

export function TimerContainer() {
    const title = useSelector<RootState, string>(selectTitleTimer);
    const taskName = useSelector<RootState, string>(selectFirstTaskName);
    const status = useSelector<RootState, TimerStatus>(selectStatusTimer);
    const totalSeconds = useSelector<RootState, number>(selectTotalSecondsTimer);
    const canStart = useSelector<RootState, boolean>(selectCanStart);
    const canStop = useSelector<RootState, boolean>(selectCanStop);
    const canPause = useSelector<RootState, boolean>(selectCanPause);
    const canProlongTime = useSelector<RootState, boolean>(selectCanProlongTime);
    const canResume = useSelector<RootState, boolean>(selectCanResume);
    const canDone = useSelector<RootState, boolean>(selectCanDone);
    const canSkip = useSelector<RootState, boolean>(selectCanSkip);

    const dispatch = useDispatch();

    return <Timer
        minutes={Math.floor(totalSeconds / 60)}
        seconds={totalSeconds % 60}
        status={status}
        title={title}
        taskName={taskName}
        // @ts-ignore
        onStart={canStart ? () => dispatch(timerStartAsync(false)):undefined}
        onStop={canStop ? () => dispatch(timerStop()):undefined}
        onPause={canPause ? () => dispatch(timerPause()):undefined}
        onProlongTime={canProlongTime ? () => dispatch(timerProlong(60)):undefined}
        // @ts-ignore
        onResume={canResume ? () => dispatch(timerStartAsync(true)):undefined}
        // @ts-ignore
        onDone={canDone ? () => dispatch(manualTimerDone()):undefined}
        onSkip={canSkip ? () => dispatch(timerSkip()):undefined}/>;
}
