import React from "react";
import styles from './pomidorinfocard.module.css'
import {Text} from "../../Text";

export interface IPomidorInfoProps{
    count:number | null;
}

export function PomidorInfoCard({count}:IPomidorInfoProps) {
    if (!count) {
        return (
            <div className={styles.wrapper}>
                <img src='lazy_pomidor.png' className={styles.lazyPomidor} alt="no file"/>
            </div>
        );
    }

    return (
        <div className={styles.wrapper}>
            <div className={styles.topContent}>
                <img src='hardworking_pomidor.png' alt="no-file"/>
                <Text size={24} bold={true} color='grey99'>{`x ${count}`}</Text>
            </div>
            <div className={styles.bottomContent}>
                <span style={{margin:'auto'}}>
                    <Text size={24} color='white' bold={true}>{`${count} помидора`}</Text>
                </span>
            </div>
        </div>
    );
}