import React, {useEffect} from "react";
import './App.css';
import {Header} from "./Header";
import {Home} from "./Home";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import {Statistics} from "./Statistics";
import {Provider} from "react-redux";
import {applyMiddleware, legacy_createStore as createStore} from "redux";
import {rootReducer} from "./store/reducers";
import {composeWithDevTools} from "@redux-devtools/extension";
import thunk from "redux-thunk";
import {checkNotificationsSupport} from "./store/utils/notifications-api";
import {saveStateToStorage} from "./store/utils/storage-api";

const store = createStore(rootReducer, composeWithDevTools(applyMiddleware(thunk)));
store.subscribe(() => {
    saveStateToStorage(store.getState());
})

function App() {
    useEffect(() =>{
        checkNotificationsSupport()
    },[]);

    return (
        <Provider store={store}>
            <BrowserRouter>
                <div className="App">
                    <Header/>
                    <Routes>
                        <Route path={`${process.env.PUBLIC_URL}/`} element={<Home/>}/>
                        <Route path={`${process.env.PUBLIC_URL}/statistics`} element={<Statistics/>}/>
                        <Route path={`${process.env.PUBLIC_URL}/*`} element={<Home/>}/>
                    </Routes>
                </div>
            </BrowserRouter>
        </Provider>
  );
}

export default App;
