import {ActionCreator} from "redux";
import {
    ADD_TASK,
    AddTaskAction, COMPLETE_TASK,
    CompleteTaskAction,
    DEC_POMIDORS,
    DecPomidorsAction, DELETE_COMPLETED_TASK,
    DELETE_TASK, DeleteCompletedTaskAction,
    DeleteTaskAction,
    EDIT_TITLE_TASK,
    EditTitleTaskAction,
    INC_POMIDORS,
    IncPomidorsAction,
    MOVE_TASK,
    MoveTaskAction,
    SELECT_TASK,
    SelectTaskAction
} from "../actions/taskList";

export const addTask:ActionCreator<AddTaskAction> = (title:string) => {
    return {type:ADD_TASK, title:title};
}

export const selectTask:ActionCreator<SelectTaskAction> = (id:number) => {
    return {type:SELECT_TASK, id};
}

export const incPomidor:ActionCreator<IncPomidorsAction> = (idTask:number) => {
    return {type:INC_POMIDORS, idTask};
}

export const decPomidor:ActionCreator<DecPomidorsAction> = (idTask:number) => {
    return {type:DEC_POMIDORS, idTask};
}

export const moveTask:ActionCreator<MoveTaskAction> = (dragIndex:number, hoverIndex:number) => {
    return {type:MOVE_TASK, dragIndex, hoverIndex};
}

export const deleteTask:ActionCreator<DeleteTaskAction> = (idTask:number) => {
    return {type:DELETE_TASK, idTask};
}

export const editTitleTask:ActionCreator<EditTitleTaskAction> = (idTask:number, newTitle:string) => {
    return {type:EDIT_TITLE_TASK, idTask, newTitle};
}

export const completeTask:ActionCreator<CompleteTaskAction> = (idTask:number) => {
    return {type:COMPLETE_TASK, idTask};
}

export const deleteCompletedTask:ActionCreator<DeleteCompletedTaskAction> = (idTask:number) => {
    return {type:DELETE_COMPLETED_TASK, idTask};
}