import React, {ReactNode} from "react";
import classNames from "classnames";
import styles from "./image.module.css";

interface IImageProps{
    type:'focus' | 'pauseTime' | 'numberOfStops';
    disabled?:boolean;
}

function getImageContent(type:'focus' | 'pauseTime' | 'numberOfStops'):ReactNode{
    switch (type){
        case 'focus':
            return (
                <>
                    <path d="M57.3158 111.632C87.3136 111.632 111.632 87.3136 111.632 57.3158C111.632 27.318 87.3136 3 57.3158 3C27.318 3 3 27.318 3 57.3158C3 87.3136 27.318 111.632 57.3158 111.632Z" strokeWidth="5" strokeLinecap="round" strokeLinejoin="round" />
                    <path d="M57.5 95C78.2107 95 95 78.2107 95 57.5C95 36.7893 78.2107 20 57.5 20C36.7893 20 20 36.7893 20 57.5C20 78.2107 36.7893 95 57.5 95Z" strokeWidth="5" strokeLinecap="round" strokeLinejoin="round" />
                    <path d="M57.5 78C68.8218 78 78 68.8218 78 57.5C78 46.1782 68.8218 37 57.5 37C46.1782 37 37 46.1782 37 57.5C37 68.8218 46.1782 78 57.5 78Z" strokeWidth="5" strokeLinecap="round" strokeLinejoin="round" />
                </>
            );
        case 'pauseTime':
            return (
                <>
                    <path d="M57.3158 111.632C87.3136 111.632 111.632 87.3136 111.632 57.3158C111.632 27.318 87.3136 3 57.3158 3C27.318 3 3 27.318 3 57.3158C3 87.3136 27.318 111.632 57.3158 111.632Z" strokeWidth="5" strokeLinecap="round" strokeLinejoin="round"/>
                    <path d="M57.3154 30.158V57.3159L70.8944 70.8948" strokeWidth="5" strokeLinecap="round" strokeLinejoin="round"/>
                </>
            );
        case 'numberOfStops':
            return (
                <>
                    <path d="M57.3158 111.632C87.3136 111.632 111.632 87.3136 111.632 57.3158C111.632 27.318 87.3136 3 57.3158 3C27.318 3 3 27.318 3 57.3158C3 87.3136 27.318 111.632 57.3158 111.632Z" strokeWidth="5" strokeLinecap="round" strokeLinejoin="round"/>
                    <path d="M21 20L95 94" strokeWidth="5" strokeLinecap="round" strokeLinejoin="round"/>
                </>
            );
    }
}

export function Image({ type, disabled = false }:IImageProps){
    return (
        <svg viewBox="0 0 115 115" className={
            classNames(styles.image, {[styles.focus]: type === 'focus', [styles.pauseTime]: type === 'pauseTime', [styles.numberOfStops]: type === 'numberOfStops', [styles.disabled]: disabled})}>
            {getImageContent(type)}
        </svg>
    )
}