import {Reducer} from "redux";
import {CHANGE_TITLE, NewTaskFormActions} from "../actions/newTaskForm";

export type NewTaskFormState = {
    title:string;
}

export const initialNewTaskFormState:NewTaskFormState ={
    title:''
}

export const newTaskFormReducer:Reducer<NewTaskFormState, NewTaskFormActions> = (state=initialNewTaskFormState, action)=>{
    switch (action.type){
        case CHANGE_TITLE:
            return{
                ...state,
                title:action.value
            }
        default:
            return state;
    }
}