import React, {ReactNode} from "react";

export interface IItem{
    content:ReactNode;
    id:string;
    onClick?:(id:string)=>void;
    className?:string;
    As?:'a' | 'li' | 'button' | 'div';
    href?:string;
}

export interface IGenericListProps{
    list:IItem[];
}

const NOOP = () => {}

export function GenericList({list}:IGenericListProps)
{
    return (
        <>
            {
                list.map(({As = 'div', className, onClick=NOOP, id, content}) => (
                    <As className={className} onClick={() => onClick(id)} key={id}>
                        {content}
                    </As>
                ))
            }
        </>
    )
}