import React from 'react';
import styles from './timer.module.css'
import {TimerStatus} from "../../store/types";
import {Button} from "../Button";
import {EIcons, Icon} from "../../Icons";
import {Text} from "../../Text";
import classNames from "classnames";

interface ITimerProps{
    minutes:number;
    seconds:number;
    status:TimerStatus,
    onStart?:()=>void;
    onStop?:()=>void;
    onResume?:()=>void;
    onSkip?:()=>void;
    onPause?:()=>void;
    onDone?:()=>void;
    onProlongTime?:()=>void;
    title:string;
    taskName:string;
}

export function Timer({minutes, seconds, status, onStart, onStop, onResume, onSkip, onPause, onDone, onProlongTime, title, taskName}:ITimerProps) {
    return (
        <div style={{display:"flex", flexDirection:"column", flexGrow:1}}>
            <div className={classNames(styles.header, {[styles.started]:status === TimerStatus.Started, [styles.pause]:status === TimerStatus.OnPause})}>
                <Text bold={true} color='white'>{taskName}</Text>
                <Text color='white'>{title}</Text>
            </div>
            <div className={styles.area}>
                <div className={styles.content}>
                    <div className={styles.timeWithProlongateContainer}>
                        <div className={styles.stubContainer} />

                        <div className={styles.timeContainer}>
                            <span className={classNames(styles.time, {[styles.startedText]:status === TimerStatus.Started, [styles.pauseText]:status === TimerStatus.OnPause})}>
                                {`${minutes.toLocaleString(undefined, {minimumIntegerDigits:2})} : ${seconds.toLocaleString(undefined, {minimumIntegerDigits:2})}`}
                            </span>
                        </div>

                        <div style={{width:'50px', display:'flex'}}>
                            {onProlongTime && <button onClick={onProlongTime} className={styles.prolongTimeButton}><Icon iconName={EIcons.Plus}/></button>}
                        </div>
                    </div>
                    <Text>{`Задача - ${taskName}`}</Text>
                    <div className={styles.buttonsArea}>
                        {onStart && <Button onClick={onStart}>Старт</Button>}
                        {onPause && <Button onClick={onPause} type='alarm'>Пауза</Button>}
                        {onStop && <Button onClick={onStop} type='alarm'>Стоп</Button>}
                        {onResume && <Button onClick={onResume}>Продолжить</Button>}
                        {onSkip && <Button onClick={onSkip}>Пропустить</Button>}
                        {onDone && <Button onClick={onDone}>Сделано</Button>}
                    </div>
                </div>
            </div>
        </div>
  );
}
