export function getFocus(timeOfWork:number, timeOfPause:number):number {
    if (timeOfWork === 0){
        return 0;
    }

    return Math.floor((timeOfWork/(timeOfWork + timeOfPause))*100);
}

export function secondsToMinutes(seconds:number):number {
    return Math.floor((seconds / 60)*10)/10;
}

export function secondsToHours(seconds:number):number {
    return Math.floor((seconds / 3600)*10)/10;
}
