import {RootState} from "../reducers";
import {TaskListState} from "../reducers/taskListReducer";
import {StatisticsState} from "../reducers/statisticsReducer";
import {IDayliStatistics, TypeWeek} from "../types";
const taskListNameItem = 'taskList';
const statisticsNameItem = 'statistics';

export const saveStateToStorage = (state:RootState) => {
    saveTaskList(state.taskList);
    saveStatistics(state.statistics);
};

export const loadTaskListFromStorage:()=> TaskListState | undefined = () => {
    return loadObject<TaskListState>(taskListNameItem, serializedState=>JSON.parse(serializedState));
};

export const loadStatisticsFromStorage:()=> StatisticsState | undefined = () => {
    return loadObject<StatisticsState>(statisticsNameItem, serializedState=> {
        const jsonObject:JsonStatistics  = JSON.parse(serializedState);
        return {
            typeWeek:jsonObject.typeWeek,
            dayWeek:jsonObject.dayOfWeek,
            history:convertJsonObjectToMap(jsonObject.history)
        }
    });
};

interface JsonStatistics{
    typeWeek:TypeWeek;
    dayOfWeek:number | null;
    history:any;
}

const saveTaskList = (taskList:TaskListState) => {
    saveObject(taskListNameItem, () => taskList);
};


const saveStatistics = (statisticsState:StatisticsState) => {
    saveObject<JsonStatistics>(statisticsNameItem, () => {
        return {
            typeWeek:statisticsState.typeWeek,
            dayOfWeek:statisticsState.dayWeek,
            history:convertMapToJsonObject(statisticsState.history)
        }
    });
};

const saveObject = <T,>(nameItem:string, getObj:()=>T) => {
    try {
        const serializedStatistics = JSON.stringify(getObj());
        localStorage.setItem(nameItem, serializedStatistics);
    } catch {
    }
}

const loadObject = <T,>(nameItem:string, parseObj:(serialisedState:string)=>T) => {
    try {
        const serializedState = localStorage.getItem(nameItem);
        if (serializedState === null) {
            return undefined;
        }

        return parseObj(serializedState);
    } catch (err) {
        return undefined;
    }
};

const convertMapToJsonObject = (map:Map<number, IDayliStatistics>):any => {
    const jsObj:any = {};
    map.forEach(function (value, key) {
        jsObj[key] = value;
    });
    return jsObj;
}

const convertJsonObjectToMap = (jsObj:any):Map<number, IDayliStatistics> => {
    const map = new Map<number, IDayliStatistics>();
    const arrayOfMapEntries = Object.entries<IDayliStatistics>(jsObj);
    for (const [key, value] of arrayOfMapEntries) {
        map.set(Number(key), value);
    }
    return map;
}