import {ActionCreator} from "redux";
import {
    SET_DAY_OF_WEEK,
    SET_TYPE_WEEK, SetDayOfWeekAction,
    SetTypeWeekAction, WRITE_POMIDOR_COMPLETE_EVENT, WritePomidorCompleteEventAction
} from "../actions/statistics";
import {TypeWeek} from "../types";

export const setTypeWeek:ActionCreator<SetTypeWeekAction> = (typeWeek:TypeWeek) => {
    return {type:SET_TYPE_WEEK, typeWeek:typeWeek};
}

export const setDayOfWeek:ActionCreator<SetDayOfWeekAction> = (dayOfWeek:number) => {
    return {type:SET_DAY_OF_WEEK, dayOfWeek:dayOfWeek};
}

export const writePomidorCompleteEvent:ActionCreator<WritePomidorCompleteEventAction> = (workTime:number, pauseTime:number, numberOfStops:number) => ({
    type:WRITE_POMIDOR_COMPLETE_EVENT,
    workTime,
    pauseTime,
    numberOfStops
})