import {RootState} from "../reducers";
import {IDayliStatistics, TypeWeek} from "../types";
import {getOnlyDate, getStartOfWeek, getToday} from "../../utils/date-api";
import addDays from "date-fns/addDays";
import subDays from "date-fns/subDays";

export function getEmptyDailyStatistics():IDayliStatistics{
    return {
        timeOfWork:0,
        timeOfPause:0,
        numberOfStops:0,
        numberPomidors:0,
    }
}

export const selectTypeWeek = (state:RootState) => state.statistics.typeWeek;

export const selectStartDate = (state:RootState) => {
    let today = getToday();

    switch (state.statistics.typeWeek) {
        case TypeWeek.Last:
            today = subDays(today, 7);
            break;
        case TypeWeek.BeforeLast:
            today = subDays(today, 14);
            break;
    }

    return getOnlyDate(getStartOfWeek(today));
}

export function selectDayStatistics(state:RootState):IDayliStatistics | null{
    const {dayWeek, history} = state.statistics;
    if (dayWeek === null){
        return null;
    }

    const selectedDate = getOnlyDate(addDays(selectStartDate(state), dayWeek));

    if (!history.has(selectedDate.getTime())){
        return null;
    }

    return history.get(selectedDate.getTime()) ?? null;
}

export function selectDayWeekName(state:RootState):string | null{
    const {dayWeek} = state.statistics;
    if (dayWeek === null){
        return null;
    }

    switch (dayWeek) {
        case 0:
            return 'Понедельник';
        case 1:
            return 'Вторник';
        case 2:
            return 'Среда';
        case 3:
            return 'Четверг';
        case 4:
            return 'Пятница';
        case 5:
            return 'Суббота';
        case 6:
            return 'Воскресенье';
    }

    return null;
}

export function selectDataSet(state:RootState):IDayliStatistics[]{
    const dataSet:IDayliStatistics[] = [];

    const startDate = selectStartDate(state);
    const history = state.statistics.history;

    for(let i = 0;i < 7;i++)
    {
        const today = addDays(startDate, i);
        const dayliStatistics = history.get(today.getTime()) ?? getEmptyDailyStatistics();
        dataSet.push(dayliStatistics);
    }

    return dataSet;
}
