import {Reducer} from "redux";
import {
    ADD_TASK,
    COMPLETE_TASK,
    DEC_POMIDORS,
    DELETE_COMPLETED_TASK,
    DELETE_TASK,
    EDIT_TITLE_TASK,
    INC_POMIDORS,
    MOVE_TASK,
    SELECT_TASK,
    TaskListActions
} from "../actions/taskList";
import {loadTaskListFromStorage} from "../utils/storage-api";
import {ITask} from "../types";

export type TaskListState = {
    tasks:ITask[];
    completedTasks:ITask[];
    selectedId:number;
}

const initialTaskListState:TaskListState ={
    tasks:[],
    completedTasks:[],
    selectedId:-1
}

export const taskListReducer:Reducer<TaskListState, TaskListActions> = (state=loadTaskListFromStorage()??initialTaskListState, action)=>{
    switch (action.type){
        case ADD_TASK:
            const maxId = state.tasks.length === 0?0:Math.max(...state.tasks.map(task => task.id), ...state.completedTasks.map(task => task.id));
            const newTasks = [...state.tasks, {id:maxId + 1, title:action.title, pomidors:1}];
            return{
                ...state,
                tasks: newTasks
            }
        case DELETE_TASK: {
            const newTasks = state.tasks.filter(task => task.id !== action.idTask);
            return {
                ...state,
                tasks: newTasks
            }
        }
        case SELECT_TASK:
            return{
                ...state,
                selectedId: action.id
            }
        case INC_POMIDORS: {
            const newTasks = [...state.tasks];
            const task = newTasks.find(t => t.id === action.idTask);
            task!.pomidors++;

            return {
                ...state,
                tasks: newTasks
            }
        }
        case DEC_POMIDORS: {
            const newTasks = [...state.tasks];
            const task = newTasks.find(t => t.id === action.idTask);
            if (!task || task.pomidors === 1){
                return state;
            }
            task!.pomidors--;

            return {
                ...state,
                tasks: newTasks
            }
        }
        case MOVE_TASK: {
            const newTasks = Array.from(state.tasks);
            const [removed] = newTasks.splice(action.dragIndex, 1);
            newTasks.splice(action.hoverIndex, 0, removed);

            return {
                ...state,
                tasks: newTasks
            }
        }
        case EDIT_TITLE_TASK: {
            const newTasks = Array.from(state.tasks);
            const task = newTasks.find(t => t.id === action.idTask);
            if (!task){
                return state;
            }
            task.title = action.newTitle;

            return {
                ...state,
                tasks: newTasks
            }
        }
        case COMPLETE_TASK:{
            const task = state.tasks.find(task => task.id === action.idTask);
            if (!task){
                return state;
            }

            const newTasks = state.tasks.filter(task => task.id !== action.idTask);
            return {
                ...state,
                tasks: newTasks,
                completedTasks:[task, ...state.completedTasks]
            }
        }
        case DELETE_COMPLETED_TASK: {
            const newTasks = state.completedTasks.filter(task => task.id !== action.idTask);
            return {
                ...state,
                completedTasks: newTasks
            }
        }
        default:
            return state;
    }
}