import React, {ReactNode, useCallback} from 'react';
import styles from './button.module.css'
import classNames from "classnames";

export interface IButtonProps{
    children:ReactNode;
    type?:'normal' | 'alarm';
    onClick?:()=>void;
    disabled?:boolean
}

export function Button({ children, type = 'normal', onClick, disabled }:IButtonProps) {
    const clickHandle = useCallback(() => {
        if (onClick) {
            onClick();
        }
    },[onClick]);

    return (
        <button className={classNames(styles.button, {[styles.normal]:type === 'normal', [styles.alarm]:type === 'alarm', [styles.disabled]:disabled})}
                onClick={clickHandle} disabled={disabled}>
            {children}
        </button>
    );
}
