import {Text} from "../../../Text";
import {Button} from "../../Button";
import {EIcons, Icon, IconColor} from "../../../Icons";
import React from "react";
import styles from "./deletetaskmodal.module.css";

interface IDeleteTaskModal{
    onDelete:()=>void;
    onCancel:()=>void;
}

export function DeleteTaskModal({onDelete, onCancel}:IDeleteTaskModal) {
    return (
        <div className={styles.content}>
            <Text size={24}>Удалить задачу?</Text>

            <Button type='alarm' onClick={onDelete}>Удалить</Button>

            <button className={styles.buttonCross} onClick={onCancel}>
                <Icon iconName={EIcons.Cross} color={IconColor.GreyC4}/>
            </button>

            <span className={styles.spanCancel} onClick={onCancel}>
                <Text size={16}>Отмена</Text>
            </span>
        </div>
    );
}
