import {TypeWeek} from "../types";

export const SET_TYPE_WEEK = 'SET_TYPE_WEEK';

export type SetTypeWeekAction = {
    type: typeof SET_TYPE_WEEK;
    typeWeek:TypeWeek;
}

export const SET_DAY_OF_WEEK = 'SET_DAY_OF_WEEK';

export type SetDayOfWeekAction = {
    type: typeof SET_DAY_OF_WEEK;
    dayOfWeek:number;
}

export const WRITE_POMIDOR_COMPLETE_EVENT = 'WRITE_POMIDOR_COMPLETE_EVENT';

export type WritePomidorCompleteEventAction = {
    type: typeof WRITE_POMIDOR_COMPLETE_EVENT;
    workTime:number;
    pauseTime:number;
    numberOfStops:number;
}

export type StatisticsActions = SetTypeWeekAction | WritePomidorCompleteEventAction | SetDayOfWeekAction;