import React from 'react';
import styles from './header.module.css'
import {Link} from "react-router-dom";
import {DiagrammIcon} from "./DiagrammIcon";

export function Header() {
    return (
        <header className={styles.header}>
            <Link to={`${process.env.PUBLIC_URL}/`} className={styles.link}>
                <div style={{display:"flex", flexDirection:'row', gap:'12px', alignItems:'center'}}>
                    <img src='hardworking_pomidor.png' alt="no -image" className={styles.logoImage}/>
                    <span className={styles.logoText}>POMODORO_BOX</span>
                </div>
            </Link>
            <Link to={`${process.env.PUBLIC_URL}/statistics`} className={styles.link}>
                <div className={styles.menuContainer}>
                    <DiagrammIcon/>
                    <span className={styles.menuText}>Статистика</span>
                </div>
            </Link>
        </header>
  );
}
