import React from 'react';
import {EIcons, Icon, IconColor} from "../../../../../Icons";

interface IMenuItemProps{
    iconName:EIcons;
    text:string;
}

export function MenuItem({ iconName, text }:IMenuItemProps) {
    return (
        <>
            <Icon iconName={iconName} color={IconColor.Green}/>
            <span>{text}</span>
        </>
  );
}
