import React from 'react';
import styles from './task.module.css'
import {Menu} from "./Menu";

export interface ITaskProps{
    id:number;
    title:string;
    isSelected:boolean;
    pomidors:number,
    onIncPomidorsClick:()=>void;
    onBlur:()=>void;
    onChangeTitle:(value:string)=>void;
    onClick:()=>void;
    onDeleteRequested:(taskId:number)=>void;
}

export function Task({title, isSelected, pomidors, onIncPomidorsClick, onBlur, onChangeTitle, id, onClick, onDeleteRequested}:ITaskProps) {
    const handleKeyPress = (event: React.KeyboardEvent<HTMLInputElement>): void => {
        if (event.key === 'Enter') {
            event.currentTarget.blur();
        }
    }

    return (
        <>
            <button title='Добавить помидоры' onClick={onIncPomidorsClick} className={styles.incPomidorButton}>{pomidors}</button>
            {isSelected &&
                <input
                    value={title} className={styles.taskInput} autoFocus
                    onBlur={onBlur} onFocus={e => e.target.select()}
                    onChange={event => onChangeTitle(event.target.value)}
                    onKeyPress={handleKeyPress}/>}
            {!isSelected && <span className={styles.taskLabel} onClick={onClick}>{title}</span>}
            <Menu taskId={id} onDeleteRequested={onDeleteRequested}/>
        </>
    );
}
