import {Reducer} from "redux";
import {SET_DAY_OF_WEEK, SET_TYPE_WEEK, StatisticsActions, WRITE_POMIDOR_COMPLETE_EVENT} from "../actions/statistics";
import {IDayliStatistics, TypeWeek} from "../types";
import {getDayOfWeek, getOnlyDate, getStartOfWeek, getToday} from "../../utils/date-api";
import {loadStatisticsFromStorage} from "../utils/storage-api";
import {getEmptyDailyStatistics} from "../selectors/statistics";
import subDays from "date-fns/subDays";

export type StatisticsState = {
    typeWeek:TypeWeek;
    dayWeek:number | null;
    history:Map<number, IDayliStatistics>;
}

function getInitialStatisticsState():StatisticsState{
    let state = loadStatisticsFromStorage();
    const today = getToday();
    if (!state){
        state = {
            typeWeek:TypeWeek.Current,
            dayWeek:getDayOfWeek(today),
            history:new Map<number, IDayliStatistics>()//getRandomHistory()//, включить для тестирования
        }
    }
    if (!state.history.has(today.getTime()))
    {
        state.history.set(today.getTime(), getEmptyDailyStatistics());
    }

    cleanupObsoleteHistoryData(state.history);

    return state;
}

function cleanupObsoleteHistoryData(history:Map<number, IDayliStatistics>):void{
    const firstDayOfWeek = getOnlyDate(getStartOfWeek(getToday()));
    const lastPossibleDate = subDays(firstDayOfWeek, 14).getTime();

    const keysToDelete:number[] = [];

    history.forEach((value: IDayliStatistics, key: number) => {
        if (key < lastPossibleDate){
            keysToDelete.push(key);
        }
    });

    keysToDelete.forEach(key => history.delete(key));
}

export const statisticsReducer:Reducer<StatisticsState, StatisticsActions> = (state= getInitialStatisticsState(), action)=> {
    switch (action.type){
        case SET_TYPE_WEEK: {
            return {
                ...state,
                typeWeek: action.typeWeek,
                dayWeek: null
            }
        }
        case SET_DAY_OF_WEEK: {
            return {
                ...state,
                dayWeek: action.dayOfWeek,
            }
        }
        case WRITE_POMIDOR_COMPLETE_EVENT:
            const today = getToday();
            let dayliStatistics = state.history.get(today.getTime())??getEmptyDailyStatistics();
            dayliStatistics = {
                ...dayliStatistics,
                numberPomidors:dayliStatistics.numberPomidors + 1,
                numberOfStops:dayliStatistics.numberOfStops + action.numberOfStops,
                timeOfWork:dayliStatistics.timeOfWork + action.workTime,
                timeOfPause:dayliStatistics.timeOfPause + action.pauseTime,
            };
            state.history.set(today.getTime(), dayliStatistics);
            cleanupObsoleteHistoryData(state.history);
            return state;
        default:
            return state;
    }
}