import React from "react";
import styles from './workinfocard.module.css'
import {Text} from "../../Text";
import {secondsToMinutes} from "../../store/utils/statistics-api";

export interface IWorkInfoCardProps{
    nameDay:string | null;
    count:number | null;
}

export function WorkInfoCard({nameDay, count}:IWorkInfoCardProps) {
    const messageReactNode = (count === null)?<Text>Нет данных</Text>:(
        <span>
            <Text>Вы работали над задачами в течении </Text>
            <Text color='red'>{`${secondsToMinutes(count)} минут`}</Text>
        </span>
    );
    return (
        <div className={styles.wrapper}>
            <div className={styles.content}>
                <Text size={24} bold={true}>{nameDay ?? 'Выберите день'}</Text>
                {messageReactNode}
            </div>
        </div>
    );
}