import React from 'react';
import styles from './menu.module.css';
import {Dropdown} from "../../../../Dropdown";
import {MenuIcon} from "./MenuIcon";
import {GenericList, IItem} from "../../../../GenericList";
import {useDispatch} from "react-redux";
import {decPomidor, incPomidor, selectTask} from "../../../../store/action-creators/taskList";
import {EIcons} from "../../../../Icons";
import {MenuItem} from "./MenuItem";

interface IMenuProps{
    taskId:number;
    onDeleteRequested:(taskId:number)=>void;
}

export function Menu({taskId, onDeleteRequested}:IMenuProps) {
    const dispatch = useDispatch();

    const button = (
        <button className={styles.menuButton}>
            <MenuIcon/>
        </button>);

    const menuItems:IItem[] =[
        {
            As:'div',
            content:<MenuItem iconName={EIcons.PlusInsideCircle} text='Увеличить'/>,
            id:'inc',
            className:styles.menuItem,
            onClick:()=>dispatch(incPomidor(taskId))
        },
        {
            As:'div',
            content:<MenuItem iconName={EIcons.MinusInsideCircle} text='Уменьшить'/>,
            id:'dec',
            className:styles.menuItem,
            onClick:()=>dispatch(decPomidor(taskId))
        },
        {
            As:'div',
            content:<MenuItem iconName={EIcons.Pen} text='Редактировать'/>,
            id:'pen',
            className:styles.menuItem,
            onClick:()=>dispatch(selectTask(taskId))
        },
        {
            As:'div',
            content:<MenuItem iconName={EIcons.Trash} text='Удалить'/>,
            id:'remove',
            className:styles.menuItem,
            onClick:()=>onDeleteRequested(taskId)
        }
    ];
    return (
        <Dropdown button={button}>
            <div className={styles.dropdown}>
                <GenericList list={menuItems}/>
            </div>
        </Dropdown>
  )
}
